/* 1. Navbar styles */
.navbar{
    position: sticky;
    font-family: 'Mulish', sans-serif;
    display: inline-block;
    overflow: hidden;
    background-color: #323031;
    height: fit-content;
    width: 100%;
}

.navbar 
.nav-item{
    float: right;
}

.nav-item{
    text-decoration: none;
    color: #999;
    padding: 1.5ch;
    margin-right: 0.5%;
}

.nav-item:hover{
    color: rgba(0, 0, 0, 0.25);
}

/* 2. Banner styles */
.banner{
    font-size: 6ch;
    padding-top: 2%;
    padding-bottom: 1%;
    color: #3D3B3C;
}